.PasswordGenerator{
  text-align: center;
  display:flex;
  width:100vw;
  height:50vh;
  align-items:center;
  justify-content: center;
}
.App h1{
  text-align: center;
  color:white
}
form{
   border:2px solid grey;
   width:30vw;
   height:40vh;
   border-radius:10px;
   background-color:rgb(1, 252, 202);
   box-shadow: rgba(40, 40, 40, 0.56) 0px 22px 70px 4px;
   padding:2.5%;
   text-align: left;
   font-size: medium;
   
}
input{
  outline: none;
}
form div{
  padding:1.4%;
}
form div input[type = "checkbox"]{
  float: right;
  height:3vh;
  width:3vw;
}
input[type = "button"]{
   float:right;
   color:white;
   background-color: black;
   border-radius:3px;
   height:5vh;
   width:5vw;
   font-size: medium;
   border:2px solid grey ;
}
input[type = "text"]{
  width:24vw;
  color:white;
   background-color: black;
   border-radius:3px;
   height:4vh;
   border:2px solid grey ;
   font-size: medium;
}
input[type="number"]{
  width:4vw;
  color:white;
   background-color: black;
   border-radius:3px;
   height:4vh;
   border:2px solid grey ;
   font-size: medium;
}
form button{
 margin-left:30%;
 background-color: rgb(248, 10, 77);
 color:white;
 height:5vh;
 font-size: medium;
 border:2px solid rgb(245, 134, 8);
 border-radius:4px;
}